<template>
  <div class="OrderDetail">
    <div class="title bg_img_none">
      <mt-cell><p slot="title">产品信息</p></mt-cell>
    </div>

    <div class="item_p"
         @click="goToPageProduct(detail.orderItemDtos[0].servicePackDto.servicePackId, detail.orderItemDtos[0].serviceOptionItem.licenceId)">
      <div class="item_img"><img :src="detail.orderItemDtos[0].servicePackDto.fileUrl"></div>
      <div class="item_content">
        <p class="item_over popup_color3">{{ detail.orderItemDtos[0].servicePackDto.title }}</p>
        <p class="popup_color8" v-html="detail.orderItemDtos[0].servicePackDto.summary"></p>
      </div>
      <div class="item_link"></div>
    </div>

    <div class="title">
      <mt-cell><p slot="title">客户信息</p></mt-cell>
    </div>
    <div class="content bg_img_none">
      <div class="container">
        <mt-cell title="客户姓名" :value="detail.customerDto.customerName"></mt-cell>
      </div>
      <div class="container">
        <mt-cell title="联系电话" :value="detail.customerDto.phoneNumber"></mt-cell>
      </div>
    </div>

    <div class="title">
      <mt-cell><p slot="title">设备信息</p></mt-cell>
    </div>
    <div class="content bg_img_none">
      <div class="container">
        <mt-cell title="品类" :value="detail.orderItemDtos[0].customerDeviceDto.productCategoriesName"></mt-cell>
      </div>
      <div class="container">
        <mt-cell title="品牌" :value="detail.orderItemDtos[0].customerDeviceDto.brandName"></mt-cell>
      </div>
      <div class="container" v-for="item in expand.otherExpand">
        <div class="container" v-if="item.objectExpandContentType !== 5">
          <mt-cell :title="item.text" :value="item.value || ' '"
                   :key="item.id"></mt-cell>
        </div>
        <div v-else>
          <div class="container">
            <mt-cell title="省" :value="address.province.name"></mt-cell>
          </div>
          <div class="container">
            <mt-cell title="市" :value="address.city.name"></mt-cell>
          </div>
          <div class="container">
            <mt-cell title="县" :value="address.county.name"></mt-cell>
          </div>
          <div class="container">
            <mt-cell title="详细地址" :value="address.desc"></mt-cell>
          </div>
        </div>
      </div>
      <div class="container">
        <mt-cell v-if="detail.orderItemDtos[0].customerDeviceDto.purchasePrice" title="购机价格"
                 :value="'￥' + detail.orderItemDtos[0].customerDeviceDto.purchasePrice"></mt-cell>
      </div>
      <div class="container">
        <mt-cell v-if="detail.orderItemDtos[0].customerDeviceDto.purchaseTime" title="购机时间"
                 :value="detail.orderItemDtos[0].customerDeviceDto.purchaseTime"></mt-cell>
      </div>
    </div>
    <div class="equipment_pic content" v-if="expand.imageExpand.length > 0">
      <div class="pic" v-for="item in expand.imageExpand">
        <div @click="previewImage(item.value)" v-if="item.value && item.value !== ''" class="img"
             :style="{background: 'url(' + item.value + ') no-repeat', backgroundSize: 'cover', backgroundPosition: 'center'}"></div>
        <div v-else class="text">未上传</div>
        <div class="nameplate">{{item.text}}</div>
      </div>
    </div>

    <div class="title">
      <mt-cell><p slot="title">支付信息</p></mt-cell>
    </div>
    <div class="content">
      <mt-cell title="订单编号" :value="detail.orderCode"></mt-cell>
      <mt-cell title="产品" :value="detail.orderItemDtos[0].serviceOptionItem.name"></mt-cell>
      <mt-cell title="支付金额" :value="'￥' + detail.paymentDto.payAmount"></mt-cell>
      <mt-cell title="支付状态">
        <span :class="payStatusColor(detail.orderStatusStr)">{{detail.orderStatusStr}}</span>
      </mt-cell>
    </div>

    <div class="qrcode" v-if="detail.orderStatusStr === '已完成'">
      <p>请用户微信扫描下方二维码，绑定手机即刻领取电子合同凭证。</p>
      <div class="img">
        <img :src="scan"/>
      </div>
    </div>

    <div class="button" v-if="detail.orderStatusStr === '已支付,待录入'" @click="goToPageFilling()">
      <mt-button type="primary" size="large">去录单,领佣金</mt-button>
    </div>

    <div class="button" v-if="detail.orderStatusStr === '未支付'" @click="goToPageFilling()">
      <mt-button type="primary" size="large">去提交</mt-button>
    </div>

  </div>
</template>
<script>
  import api from '../../../api/index2'
  import moment from 'moment'
  import wx from 'weixin-js-sdk'
  import {loadData, defaultPreHandler} from '../../../common/utils'
  import {createNamespacedHelpers} from 'vuex'
  import _ from 'lodash'

  const {mapMutations} = createNamespacedHelpers('order')

  export default {
    name: 'OrderDetail',
    data () {
      return {
        detail: {},
        index: null,
        orderId: null,
        expand: {
          imageExpand: [],
          otherExpand: [],
          expands: []
        },
        address: {
          desc: '',
          province: {
            id: null,
            name: ''
          },
          city: {
            id: null,
            name: ''
          },
          county: {
            id: null,
            name: ''
          }
        },
        previewUrl: [],
        scan: ''
      }
    },
    async created () {
      this.orderId = this.$route.query.orderId
      await this.getDetail()
      await this.getScan()
      await this.getExpands()
    },
    beforeRouteLeave (to, from, next) {
      if (to.name === 'MyOrder' && this.isPreview) {
        this.isPreview = false
        next(false)
      } else {
        next()
      }
    },
    methods: {
      ...mapMutations(['removeOrderFilling']),
      async config () {
        let url = location.href.split('#')[0]
        await this.$initWxConfig(url, ['previewFile'])
      },
      async getDetail () {
        await loadData(() => {
          return api.getOrderDetail(this.orderId)
        }, (data) => {
          if (data.orderItemDtos[0].customerDeviceDto.purchaseTime) {
            data.orderItemDtos[0].customerDeviceDto.purchaseTime = moment(data.orderItemDtos[0].customerDeviceDto.purchaseTime).format('YYYY-MM-DD')
          }
          data.orderStatusStr = this.orderStatusStr(data.orderStatus, data.paymentDto.paymentStatus, data.paymentDto.payType)
          this.detail = data
        }, defaultPreHandler)
      },
      async getExpands () {
        if (this.detail.orderItemDtos[0].customerDeviceDto.productCategoriesId) {
          let param = {
            orderId: this.orderId,
            deviceId: this.detail.orderItemDtos[0].customerDeviceDto.id
          }
          await loadData(() => {
            return api.getDeviceExpands(param)
          }, data => {
            this.expand.expands = data
          }, defaultPreHandler, true)
          this.expand.otherExpand = _.filter(this.expand.expands, function (item) {
            return item.objectExpandContentType !== 1 && item.objectExpandContentType !== 3
          })

          this.expand.imageExpand = _.filter(this.expand.expands, {objectExpandContentType: 3})
          let address = _.filter(this.expand.expands, {objectExpandContentType: 5})
          if (address.length > 0) {
            let value = JSON.parse(address[0].value)
            this.address.desc = value.addressDescription
            this.address.city.name = value.city
            this.address.city.id = value.cityId
            this.address.county.name = value.county
            this.address.county.id = value.countyId
            this.address.province.name = value.province
            this.address.province.id = value.provinceId
          }
        }
      },
      async getScan () {
        if (this.detail.orderStatusStr === '已完成') {
          let partnerId = this.detail.partnerId
          await loadData(() => {
            return api.getPartner(partnerId)
          }, data => {
            let scan = data
            this.scan = scan.qrCode
          }, defaultPreHandler, true)
        }
      },
      previewImage (url) {
        let vue = this
        if (_.isEmpty(this.previewUrl)) {
          let previewUrl = _.filter(this.expand.imageExpand, function (item) {
            return item.value && item.value !== ''
          })
          _.forEach(previewUrl, (item) => {
            this.previewUrl.push(item.value)
          })
        }
        wx.previewImage({
          current: url,
          urls: vue.previewUrl
        })
      },
      orderStatusStr (orderCode, payCode, payMethod) {
        if (payCode === 30 || orderCode === 100) {
          return '已退款'
        } else if (payCode === 50 || orderCode === 40) {
          return '已取消'
        } else {
          let arr = [orderCode, payCode, payMethod]
          let status = arr.join('')
          switch (status) {
            case '110200':
              return '已完成'
            case '60200':
              return '已完成'
            case '110201':
              return '已完成'
            case '60201':
              return '已完成'
            case '60202':
              return '已完成'
            case '110202':
              return '已完成'
            case '20100':
              return '已支付,待录入'
            case '20202':
              return '已支付,待录入'
            case '20201':
              return '已支付,待录入'
            case '21100':
              return '未支付'
            // case '20102':
            //   return '未支付'
            // case '20101':
            //   return '未支付'
            // case '2010':
            //   return '未支付'
            default:
              return ''
          }
        }
      },
      payStatusColor (status) {
        switch (status) {
          case '未支付':
            return 'color_1'
          case '已支付,待录入':
            return 'color_2'
          case '已完成':
            return 'color_0'
          default:
            return 'color_3'
        }
      },
      goToPageProduct (servicePackId, licenceId) {
        this.$router.push({
          name: 'ProductDescription',
          query: {
            servicePackId,
            licenceId
          }
        })
      },
      goToPageFilling () {
        this.removeOrderFilling()
        this.$router.push({
          name: 'OrderFilling',
          query: {
            orderId: this.orderId
          }
        })
      },
      goToPagePayList () {
        this.$router.push({
          name: 'PayList',
          query: {
            orderId: this.orderId,
            paymentId: this.detail.paymentDto.id
          }
        })
      }
    }
  }
</script>
<style scoped>
  .title p {
    font-size: 14px;
    color: #888;
  }

  .content {
    background: #fff;
    font-size: 14px;
  }

  .container {
    background: #fff;
    position: relative;
  }

  .container:not(:last-child)::after {
    content: " ";
    position: absolute;
    bottom: 0;
    left: 15px;
    width: calc(100% - 15px);
    height: 1px;
    transform-origin: 0 0;
    transform: scaleY(0.5);
    background: #e5e5e5;
  }

  .equipment_pic {
    padding: 10px 15px 0;
    font-size: 0;
  }

  .pic {
    width: 29%;
    max-width: 115px;
    max-height: 115px;
    height: 26vw;
    display: inline-block;
    text-align: center;
    margin-right: calc((13% - 15px) / 3);
  }

  .pic .img {
    width: 100%;
    height: 100%;
    color: #888;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .pic .text {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #ddd;
    font-size: 14px;
    color: #888;
  }

  .pic img {
    width: 100%;
  }

  .pic .nameplate {
    font-size: 12px;
    padding: 10px 0;
    color: #888;
    overflow: hidden;
    width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .color_0 {
    color: #26a2ff;
  }

  .color_1 {
    color: #ef4f4f;
  }

  .color_2 {
    color: #ffc107;
  }

  .color_3 {
    color: #888;
  }

  .button {
    width: 90%;
    margin: 15px auto;
  }

  .qrcode {
    background: #fff;
    margin-top: 15px;
  }

  .qrcode p {
    color: #888;
    font-size: 14px;
    line-height: 1.5rem;
    padding: 15px 15px 0;
    text-indent: 28px;
  }

  .qrcode .img {
    width: 50%;
    margin: auto;
    padding: 10px 0;
  }

  .qrcode .img img {
    width: 100%;
    text-align: center;
  }

  .item_p {
    padding: 10px 0 10px 15px;
    display: table;
    width: 100%;
    font-size: 14px;
    box-sizing: border-box;
    table-layout: fixed;
    background: #fff;
  }

  .item_img {
    display: table-cell;
    font-size: 0;
    line-height: 0;
    vertical-align: middle;
    text-align: center;
    width: 44px;
    height: 44px;
  }

  .item_img img {
    width: 100%;
    height: 100%;
  }

  .item_content {
    display: table-cell;
    padding-left: 15px;
  }

  .item_over {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .item_link {
    display: table-cell;
    position: relative;
    width: 25px;
  }

  .item_link::after {
    border: solid 2px #c8c8cd;
    border-bottom-width: 0;
    border-left-width: 0;
    content: " ";
    top: 50%;
    right: 50%;
    position: absolute;
    width: 5px;
    height: 5px;
    transform: translateY(-50%) rotate(45deg);
  }

  .popup_color3 {
    color: #333;
  }

  .popup_color8 {
    line-height: 1;
    color: #888;
    margin-top: 6px;
  }

</style>



